import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'estado-dados',   templateUrl: 'estado_dados.component.html' }) export class EstadoDadosComponent implements OnInit {       displayMode: EstadoDadosDisplayModeEnum;     displayModeEnum = EstadoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = EstadoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = EstadoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = EstadoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum EstadoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


